import React from 'react';

import ViewCmp from '../component/ViewCmp';
import ProjectInfoCmp from '../component/ProjectInfoCmp';
import ViewBottomCmp from '../component/ViewBottomCmp';

import '../css/item.scss';

import prjList from './prjList.json';

// image import
import imgVisua1 from '../images/work/ourwork-aritaum-img@2x.jpg';

class Project1 extends React.Component {
    constructor(props){
        super(props);

        // json 파일에서 가져올 리스트 번호
        this.idx = 1;

        this.state = {
            type: prjList[this.idx].type,        //project type
            cate: prjList[this.idx].cate,        //sorting category / 카테고리의 클래스를 받아 sorting
            title: prjList[this.idx].title,      //title
            desc: prjList[this.idx].desc,        //description
            mission: prjList[this.idx].mission,  //mission
            partner: prjList[this.idx].partner,  //partner
            launch: prjList[this.idx].launch,    //launch
            url: prjList[this.idx].url,          //url
            img:imgVisua1,                       //visual image
        }
    }

    componentDidMount(){
        // console.log(prjList[1]);
        // console.log(prjList[1].type);
    }


    render(){
        return (
            <React.Fragment>

                <ViewCmp
                    type={this.state.type} 
                    title={this.state.title} 
                    desc={this.state.desc} 
                    cate={this.state.cate} 
                    img={this.state.img} 
                    mission={this.state.mission} 
                    partner={this.state.partner} 
                    launch={this.state.launch} 
                    url={this.state.url} 
                />

                <div className="cont inner_left">

                    <ProjectInfoCmp 
                        mission={this.state.mission}
                        partner={this.state.partner}
                        launch={this.state.launch}
                        url={this.state.url}
                    />


                    {/* ProjectInfoCmp 컴포넌트로 변경전 */}
                    {/* <ul className="prinfo">
                        <li>
                            <p className="cate">Mission</p>
                            <p className="cont">
                                {this.state.mission}
                            </p>
                        </li>
                        <li>
                            <p className="cate">Partner</p>
                            <p className="cont">
                                {this.state.partner}
                            </p>
                        </li>
                        <li>
                            <p className="cate">launch</p>
                            <p className="cont">
                                {this.state.launch}
                            </p>
                        </li>
                        <li>
                            <p className="cate">Link</p>
                            <p className="cont">
                                {this.state.url}
                            </p>
                        </li>
                    </ul> */}



                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>
                    상세 컨텐츠<br/>

                    
                </div>

                <ViewBottomCmp/>

            </React.Fragment>
        )
    }
}

export default Project1;