import React, { Component } from 'react';
import ListCmp from '../component/ListCmp';

import Isotope from 'isotope-layout';
import imagesLoaded from 'imagesloaded';

// image import
import img1 from '../images/work/ourwork-aritaum-img.jpg';
import img2 from '../images/work/ourwork-asiana-img.jpg';
import img3 from '../images/work/ourwork-lottehotel-img.jpg';
import img4 from '../images/work/ourwork-lottetower-img.jpg';
import img5 from '../images/work/ourwork-namuh-img.jpg';
import img6 from '../images/work/ourwork-samsunghospital-img.jpg';

import prjList from '../projects/prjList.json';


class Work extends Component {
  constructor(props){
    super(props);
    this.state = {
        listData: [
            {
                type: prjList[1].type,
                title: prjList[1].title,
                cate: prjList[1].cate,
                img:img1,
                link: 'Project1'
            },
            {
                type: prjList[2].type,
                title: prjList[2].title,
                cate: prjList[2].cate,
                img:img2,
                link: 'Project2'
            },
            {
                type: prjList[3].type,
                title: prjList[3].title,
                cate: prjList[3].cate,
                img:img3,
                link: 'Project3'
            },
            {
                type: prjList[4].type,
                title: prjList[4].title,
                cate: prjList[4].cate,
                img:img4,
                link: 'Project4'
            },
            {
                type: prjList[5].type,
                title: prjList[5].title,
                cate: prjList[5].cate,
                img:img5,
                link: 'Project5'
            },
            {
                type: prjList[6].type,
                title: prjList[6].title,
                cate: prjList[6].cate,
                img:img6,
                link: 'Project6'
            },
            {
                type: prjList[1].type,
                title: prjList[1].title,
                cate: prjList[1].cate,
                img:img1,
                link: 'Project1'
            },
            {
                type: prjList[2].type,
                title: prjList[2].title,
                cate: prjList[2].cate,
                img:img2,
                link: 'Project2'
            },
            {
                type: prjList[3].type,
                title: prjList[3].title,
                cate: prjList[3].cate,
                img:img3,
                link: 'Project3'
            },
            {
                type: prjList[4].type,
                title: prjList[4].title,
                cate: prjList[4].cate,
                img:img4,
                link: 'Project4'
            },
            {
                type: prjList[5].type,
                title: prjList[5].title,
                cate: prjList[5].cate,
                img:img5,
                link: 'Project5'
            },
            {
                type: prjList[6].type,
                title: prjList[6].title,
                cate: prjList[6].cate,
                img:img6,
                link: 'Project6'
            },
        ],
        isLoaded: false,
    }

  }


  //Loading ing
  componentDidMount(){

    // isotope안에서 state변경 위함
    // isotope안에서 this가 바뀌므로 this를 _this에 저장해서 사용
    let _this = this;

    // Isotope js
    let grid = document.querySelector('.item_wrap');

    // imagesLoaded / Detect when images have been loaded.
    imagesLoaded(grid, function(){
        console.log('loaded');

        // 로드되는 시간 동안 로딩바 노출
        _this.setState({
            isLoaded: !_this.state.isLoaded
        });

        let iso = new Isotope( grid, {
            itemSelector: '.item_card',

            percentPosition: true,
            masonry:{
                columnWidth: '.grid-sizer',
                gutter: '.grid-gutter'
            }

            // * fix size
            // masonry: {
            //     columnWidth: 770,
			// 	gutter: 140,
			// 	isFitWidth: true
            // }
        });


        // bind filter click
        let filtersElem = document.querySelector('.filters-button-group');
        let links = filtersElem.querySelectorAll('button');
        
        filtersElem.addEventListener('click', function(e) {
        
        let filterValue = e.target.getAttribute('data-filter');
        iso.arrange({ filter: filterValue });
        
        e.target.classList.add('on');

        for (let i = 0; i < links.length; i++) {
            if(links[i] === e.target) continue;
            links[i].classList.remove('on');
        }
        });

    });

  }


  //자식컴포넌트 ListCmp 의 상태값을 받아오기
  handleCreate = (childIsOpen) => {
    //   console.log('isOpen은',childIsOpen);
  }

  render(){
      return (
          <React.Fragment>

            {/* loading */}
            {this.state.isLoaded ? 
                ''
            :
                <div className="loading_box">
                   <span>Loading ...</span>
                </div>
            }


            <div className="section_copy">
                <p className="cate">Our Work</p>
                <p className="desc">
                    Business model, On-line Marketing, UX 등 IT기반의 
                    컨설팅부터 구축 및 통합까지 디지털에 관한 모든 경험을 제공합니다.
                </p>
            </div>


            <div className="inner_left">

                <div className="filters-button-group">
                    <button className="on" data-filter="*">All</button>
                    <button data-filter=".web">Web</button>
                    <button data-filter=".mobile">Mobile</button>
                    <button data-filter=".uiux">UI/UX</button>
                    <button data-filter=".branding">BRANDING</button>
                </div>

                <div className="item_wrap">

                    <div className="grid-sizer"></div>
                    <div className="grid-gutter"></div>
    
                    {/* ListCmp 맵핑 */}
                    {this.state.listData.map((list,i) => {
                        return (
                            <ListCmp 
                                type={list.type} 
                                title={list.title} 
                                img={list.img} 
                                cate={list.cate}
                                cont={list.cont}
                                link={list.link}
                                idx={i} 
                                key={i} 
                                onCreate={this.handleCreate}
                            />
                        )
                    })}
                    
                </div>
            </div>


            {/* 
                자식에서 부모로 전달하기
                ListCmp isOpen 을 어떻게 받아오기
                부모 Cmp : 콜백정의 후 자식에게 props로 전달
                자식 Cmp : this.props.콜백  불러오고 파라미터로 데이터를 전달
                콜백함수 내에서 해당 변수를 사용
            */}
          </React.Fragment>
      )
  }

}

export default Work;
